.info {
    display: grid;
    grid-template-columns: auto minmax(0, 1fr);
    grid-auto-rows: auto;
    gap: 5px;
    padding: 10px;
    border: 1px solid cornflowerblue;
    border-radius: 5px;
    align-items: center;

    margin: 10px 0;
}

.info>label {
    font-weight: bold;
    text-align: right;
}

.info>.title {
    margin: -10px -10px 0 -10px;
    grid-column: span 2;
    border-radius: 5px 5px 0 0;
    background-color: cornflowerblue;
    color: white;
    padding: 5px;
}