.container {
    display: flex;
    flex-direction: column;
}

.field {
    padding: 10px;
    margin: 10px;

    gap: 10px;
    font-weight: normal;
    background: white;

    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: auto 1fr;

    grid-template-areas: 
        "name value"
        "info info"
    ;

    border-radius: 5px;
}


.field>.name {
    grid-area: name;
    font-weight: bold;
}

.field>.value {
    grid-area: value;
}

.field>.info {
    grid-area: info;
    opacity: 0.5;
    font-style: italic;
}