.order {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-columns: minmax(0, 1fr);
    padding: 10px 20px;             
    border-radius: 10px;
    animation: fade-in 0.3s normal;
}


@keyframes fade-in {
    0% {
      opacity: 0;
      transform: translate(0, -20px);
    }
    100% {
      opacity: 1;
      transform: translate(0, 0); 
    }
  }



.badges {
    display: flex;
    gap: 10px;
    align-items: center;
}

.badge {
    padding: 5px;
    border-radius: 5px;
    font-size: 15px; 
    font-weight: normal;

}

.badge.seller {
    color: white;
    background-color: #20dab7;
}

.badge.locked {
    color: white;
    background-color: #2050da;
}


.badge.lockExpired {
    color: white;
    background-color: #8594c1;
}

.badge.disabled {
    color: rgb(54, 22, 22);
    background-color: #cab4c6;
}

.order.selected {
    background-color: var(--back-selected);
}
.header {
    display: grid;
    grid-template-columns: 70px 70px auto 70px auto 1fr auto;
    font-size: 30px;
    gap: 20px;
    cursor: pointer;
}

.header>.amount,
.header>.price
 {
    text-align: right;
    justify-self: end;
}

.header>.coin,
.header>.at,
.header>.priceUnits
{
    color: var(--color-dim);
}

.order:hover {
    background-color: var(--back-selected);
    box-shadow: 0 5px 50px rgba(0,0,0 ,0.1);
}

.info {
    display: grid;
    grid-template-columns: auto minmax(0, 1fr);
    grid-auto-rows: auto;
    grid-template-rows: auto;
    gap: 10px;
}

.info>.buttons {
    grid-column: span 2;
    display: flex;
    gap: 10px;
}