.info {
    display: grid;
    grid-template-columns: auto minmax(0, 1fr);
    grid-auto-rows: auto;
    gap: 5px;
    padding: 10px;
    border: 1px solid cornflowerblue;
    border-radius: 5px;
    align-items: center;

    margin: 10px 0;
}

.info>label {
    font-weight: bold;
    text-align: right;
}

.warning {
    color: red;
    font-weight: bold;
    margin-bottom: 10px;
}

.info>.title {
    margin: -10px -10px 0 -10px;
    grid-column: span 2;
    border-radius: 5px 5px 0 0;
    background-color: cornflowerblue;
    color: white;
    padding: 5px;
}

.badge {
    border-radius: 5px;
    padding: 5px;
    font-family: var(--font-m);
}

.ok {
    background: #a7ffa3;
}

.warn {
    background: #ff9c00;
}

.dim {
    opacity: 0.3;
    font-style: italic;
}

.pubkeys {
    display: flex;
    gap: 5px;
    align-items: center;
}