.modalContainer {
    display: grid;

    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;

   
    background: rgb(9, 27, 47, 0.85);
    
    grid-template-columns: minmax(0, 1fr);
    grid-template-rows: 1fr minmax(0, auto) 2fr;
    grid-template-areas: "." "content" ".";

    animation: fade-in 0.3s normal;


    justify-items: center;
}

.modalContainer>.content {
  grid-area: content;

  display: grid;
  grid-template-rows: minmax(0, 1fr);
}

.modalContainer.absolute {
  position: absolute;
}
@keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }

