.inputContainer {
    display: grid;
    grid-template-rows: auto;
}
.inputContainer.canError {
    grid-template-rows: auto minmax(0, 30px);
}

.err {
    color:red;
    font-style: italic;
    font-weight: normal;

    border-radius: 0 0 10px 10px;
    background: rgba(255, 0,0,0.1);
    padding: 5px;
    animation: fade-in 0.3s normal;

}

@keyframes fade-in {
    0% {
      opacity: 0;
      transform: translate(0, -20px);
    }
    100% {
      opacity: 1;
      transform: translate(0, 0); 
    }
  }



.input {
    font-size: 20px;
    padding: 10px;
    border-radius: 10px;
    border-width: 1px;
    font-family: var(--font-m);
    z-index: 1;

}

.input.error {
    border-color: red;
}

.info {
    color: var(--color-dim);
    font-style: italic;
    font-weight: normal;
    text-decoration: none;
}

.button {
    font-size: 20px;
    font-family: var(--font);
    font-weight: normal;
    border-radius: 10px;

    --back: #5033ff;

    border: 2px solid var(--back);
    color: var(--back);

    padding: 10px;


    cursor: pointer;
}

.button:focus {
    background: #dbc6ff;
}

@keyframes move {
    0% {
      background-position: 0 0;
    }
    100% {
      background-position: 50px 0px;
    }
  }

.button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.button.loading:disabled {
    background-size: 50px 50px;
    --progress-color1: #abdaff;
    --progress-color2: #8ec8ff;

    background-image: linear-gradient(
        -45deg,
        var(--progress-color1) 25%,
        var(--progress-color2) 25%,
        var(--progress-color2) 50%,
        var(--progress-color1) 50%,
        var(--progress-color1) 75%,
        var(--progress-color2) 75%,
        var(--progress-color2)
      );
      animation: move 2s linear infinite;
}

.button:hover:not(:active):not(:disabled) {
    background: var(--back);
    color: white;
}

.hash {
    font-family: var(--font-m);
    text-decoration: none;
    word-break: break-all;
}

.infoBox {
    display: grid;
    grid-template-rows: auto;
    grid-auto-rows: 1fr;
    border-radius: 5px;
    background: #ffbdfc;
    color: #9b0373;
    font-size: 14px;
    font-weight: normal;
    padding: 10px;
    font-style: italic;
    gap: 5px;

    cursor: pointer;
    opacity: 0.8;

    margin-top: 5px;
}

.infoBox:hover {
    opacity: 1;
}

.infoBox:not(.open)>.content{
    display: none;
}

.infoBox>.title {
    font-weight: bold;
}

.infoGroup {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    gap: 5px;
    margin-bottom: 10px;
}

.spinnerContainer {
    position: absolute;
    display: flex;
    gap: 10px;
    color: mediumslateblue;

    justify-self: center;
    align-self: center;

    align-items: center;

    opacity: 1;
    transition: opacity 0.5s;
}

.spinnerContainer.hide {
    opacity: 0;
}

@keyframes fade-in {
    0% {
      opacity: 0;
    }
    100% {
      opacity: 1;
    }
  }



.spinner {
    width: 32px;
    height: 32px;
    border-radius: 32px;
    border: 5px solid mediumslateblue;
   

    border-left-color: transparent;
    animation: spin 1s linear infinite running;

}

@keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
}