.modal {
    width: 700px;
    min-height: 300px;

    display: grid;
    
    grid-template-rows: auto 1fr auto;


    grid-template-areas: "title" "content" "buttons" ;
    gap: 20px;
    background: white;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
    
    box-sizing: border-box;
    padding: 20px;

    overflow-y: auto;

    margin: 30px 0;
}


.modal>.title {
    grid-area: title;
    font-size: 20px;
}

.modal>.content {
    grid-area: content;
    font-weight: normal;
}

.modal>.buttons {
    grid-area: buttons;
    display: flex;
    gap: 10px;
    justify-self: end;
}