.empty {
    padding: 20px;
    border-radius: 10px;
    background: white;
    text-align: center;
    animation: fade-in 0.3s normal;
}

@keyframes fade-in {
    0% {
      opacity: 0;
      transform: translate(0, -20px);
    }
    100% {
      opacity: 1;
      transform: translate(0, 0); 
    }
  }
