.item {
    display: grid;
    grid-template-rows: auto 1fr;
    --br: 10px;
    border-radius: var(--br);

    background: white;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.2);
    animation: fade-in 0.3s normal;
}

@keyframes fade-in {
    0% {
      opacity: 0;
      transform: translate(0, -20px);
    }
    100% {
      opacity: 1;
      transform: translate(0, 0); 
    }
  }


.item>.title {
    padding: 10px;
    background-color: rgba(0,0,0,0.1);
    font-weight: normal;

    border-radius: var(--br) var(--br) 0 0;

}

.title.enabled {
    background-color: rgb(167 255 222);
    color: #0A736A;
}

.title.disabled {
    background-color: rgb(255 192 192);
    color: #730A07;
}

.dim {
    opacity: 0.3;
}

.item>.info {
    display: grid;
    grid-template-columns: auto 1fr;
    grid-auto-rows: auto;
}



.title>.disabled {
    color: red;
}

.content {
    display: grid;
    grid-template-columns: auto minmax(0, 1fr);
    grid-auto-rows: auto;
    gap: 5px 10px;
    padding: 10px;
    font-weight: normal;
    align-items: center;
}

.content>label {
    justify-self: end;
    font-weight: bold;
}

.hex {
    font-family: var(--font-m);
    word-break: break-all;
}

.algos {
    display: flex;
    gap: 5px;
}

.algos>span {
    background: #ffdb85;
    padding: 5px;
    border-radius: 5px;
    font-weight: bold;
}