.container {
    padding: 10px;
    font-size: 20px;

    display: grid;
    grid-auto-flow: column;
    grid-auto-columns: auto;
    gap: 5px;
}

@media (max-width: 768px) {
    .container {
        overflow-x: auto;
    }
}

.item {
    border-radius: 10px;
    color: var(--color);
    padding: 10px;
    text-align: center;

    transition: 0.3s background-color, 0.3s color;

    cursor: pointer;

    display: grid;
    align-items: center;
}

.item:hover,
.item.selected
{
    background-color: var(--back-selected);
    box-shadow: 0 5px 50px rgba(0,0,0 ,0.1);
}
