.body {
    display: grid;
    grid-template-rows: auto 1fr;
    grid-template-areas: "info" "shell" ;
    min-height: 100vh;
}

.info {
    grid-area: info;
    text-align: center;
    background: #5033ff;
    padding: 10px;
    color: white;
}

.info>a {
    color: white;
}

.shell {
    grid-area: shell;

    display: grid;
    grid-template-rows: auto auto 1fr auto;
    grid-template-columns: minmax(0, 1fr);
    grid-template-areas: "menu" "content" "." "title";
    
    width: 100%;
    max-width: 1000px;
    justify-self: center;
    
    background:var(--back);

    padding: 0 20px;
    background: #f0edf6;

}


.shellContent {
    display: grid;
    grid-area: content;
    grid-template-rows: minmax(0, 1fr);
    align-items: start;
}

.menu {
    grid-area: menu;
    transition: 0.3s height;
    height: calc(100vh - 100px);
}
.open .menu {
    height: 50px;
}

.title {
    grid-area: title;
    display: flex;
    justify-self: end;
}